<template>
  <div id="app">

    <h3 class="d-flex justify-content-center">Todo_test</h3>

    <div class="container">
      <div class="row justify-content-center align-items-center">
        <div class="input-group mb-3 col-8 col-xl-6">
          <input type="text" v-model="input" placeholder="please enter message" class="form-control" aria-label="Recipient's username" aria-describedby="basic-addon2">
          <div class="input-group-append">
            <button type="button" @click="add" class="btn btn-success">Add</button>
          </div>
        </div>
      </div>
    </div>

    <div class="container mt-3">
      <div class="row justify-content-center align-items-center">
        <ul class="list-group">
          <li v-for="(todo, index) in todos" :key="index" class="list-group-item">
            {{todo}}
            <button type="button" @click="del(index)" class="btn btn-dark">Del</button>
          </li>
        </ul>
      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      input: '',
      todos: []
    }

  },
  methods: {
    add() {
      let self = this
      let data = self.input
      self.todos.push(data)
      this.input = ''
    },
    del(index) {
      let self = this
      let i = index
      self.todos.splice(i, 1)
    }
  },
  components: {

  }
}
</script>

<style lang="scss">
@import "~bootstrap/scss/bootstrap";

#app {
  margin-top: 10%;
}
</style>



